@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body,
html {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  min-height: 100%;
  display: flex;
  font-family: "Source Sans Pro", sans-serif;
}

body {
  background-color: rgb(135, 206, 250);
}

p {
  color: rgb(255, 255, 255);
}


.App {
  width: 100vw;
}
.App nav {
  background-color: rgb(240, 248, 255);
  height: 60px;
  color: rgb(70, 130, 180);
  display: flex;
  align-items: center;
  padding: 0px 20px;
}

.App nav h1 {
  border-left: 3px solid rgb(70, 130, 180);
  padding-left: 4px;
}

.App .app-instructions {
  text-align: center;
}

.App .app-instructions .kwote {
  color: rgb(70, 130, 180);
}

@media (max-width: 600px) {
  #category-select,
  #category-select option,
  button {
    width: 300px;
  }
}

@media (min-width: 601px) {
  #category-select,
  #category-select option,
  button {
    width: 400px;
  }
} 

.MySelect {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#my-select {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 70px;
}

#my-select label {
  font-size: 20px;
}

#category-select,
#category-select option {
  /* width: 400px; */
  height: 40px;
  background-color: rgb(240, 248, 255);
  border: none;
  font-size: 18px;
  display: flex;
  justify-content: center;
}

select:focus {
  outline: none;
}

button {
  /* width: 400px; */
  height: 40px;
  border: none;
  -webkit-transition: 0.5s all;
  transition: 0.5s all;
  margin-top: 5px;
  background-color: rgb(70, 130, 180);
  color: rgb(255, 255, 255);
  font-size: 20px;
}

button:focus {
  outline: none;
}

button:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

@media (max-width: 600px) {
  .Quote {
    width: 90%;
  }
}

@media (min-width: 601px) {
  .Quote {
    width: 80%;
  }
}

.Quote {
  text-align: center;
  width: 80%;
  margin: 50px auto;
  color: rgb(105, 105, 105);
}

.Quote p {
  color: rgb(105, 105, 105);
}

blockquote {
  line-height: 40px;
}

